import instance from './http.config.js'

// 获取需求列表
export const getManagementListService = (param) => instance.get(`/admin/needs${param}`)
// 获取需求条件
export const getConditionsService = () => instance.get(`/admin/needs/conditions`)
// 获取籍贯
export const getProvinceRegionList = () => instance.get(`/admin/service/getProvinceRegionList?pid=-1`)
// 获取技能
export const getServiceBabelList = (param) => instance.get(`/admin/service_babel/index${param}`)
// 获取籍贯
export const getNatureList = () => instance.get(`/admin/nature/index?status=1&paginate=0`)
// 需求提交
export const postReleaseNeeds = (param) => instance.post('/admin/needs', param)
// 需求详情
export const needsInfo = id => instance.get(`/admin/needs/info?id=${id}`)
// 需求上架下架
export const getNeedsStatus = (param) => instance.put(`/admin/needs/status${param}`)
// 需求修改
export const editNeeds = (param) => instance.put('/admin/needs', param)



