import axios from 'axios'
import { Message, Loading } from 'element-ui'
import {getLoginInfo} from '../assets/common/utils.js'

const instance = axios.create({
  baseURL: process.env.BASE_API,
  headers: {
    'Content-type': 'application/json'
  },
  timeout: 300000
})
let loadingIntance = null

// 请求拦截器
instance.interceptors.request.use(config => {
  let isShowLoading = 'show'
  // 根据请求中设置的config.data字段的值来确定是否显示loading组件
  if (config.data && config.data.isShowLoading) {
    isShowLoading = config.data.isShowLoading
  }
  if (isShowLoading === 'show') {
    loadingIntance = Loading.service({ fullscreen: true })
  }
  if (getLoginInfo('Authentication-token')) {
    config.headers.common['Authentication-token'] = getLoginInfo('Authentication-token')
  }
  return config
})

// 响应拦截器
instance.interceptors.response.use(response => {
  if (loadingIntance) {
    loadingIntance.close()
  }
  if (response.status === 200 && response.data.status === 'error' && response.data.msg.indexOf('请填写入驻申请') === -1) {
    Message.error(response.data.msg)
    return Promise.reject(new Error(response.data))
  }
  // 没权限到登陆页
  if (response.data.code === 401) {
    Message.error(response.data.msg)
    window.location.href = '/'
  }
  return response.data
}, err => {
  if (loadingIntance) {
    loadingIntance.close()
  }
  console.log(err.message.indexOf('timeout'))
  if (err.message.indexOf('404') > 0) {
    Message.error(`无效访问（404）！`)
  } else if (err.message.indexOf('timeout') >= 0) {
    Message.error(`响应超时，请重试`)
  } else if (err.message.indexOf('500') > 0) {
    Message.error(`服务器错误（500）！`)
  } else if (err.message.indexOf('502') > 0) {
    Message.error(`网络错误，请联系管理员！`)
  } else if (err.message.indexOf('504') > 0) {
    Message.error(`请求失败了，请检查网络并重试！`)
  } else {
    Message.error(`错误：${err.message}`)
  }
  return Promise.reject(err)
})

export default instance



