// 将服务器返回的服务订单数据转换为需要的格式
export const transServiceOrders = (data, statusMap) => {
  // data = [...data, ...data]
  let result = data.map(item => {
    let order = {}
    let orderObject = item.order_object
    let serviceStatusMap = statusMap.serviceStatus
    let payStatusMap = statusMap.payStatus
    order['number'] = item.code
    order['orderId'] = orderObject.order_id
    order['serviceType'] = orderObject.type_name
    order['employerName'] = orderObject.user_name
    order['orderAmount'] = toCurrencyStr(orderObject.service_salary)
    order['isResidein'] = orderObject.live_home
    if (item.service_apply_time) {
      order['orderStartTime'] = formatDate(item.service_apply_time, 'yyyy-MM-dd')
    } else {
      order['orderStartTime'] = formatDate(item.service_time, 'yyyy-MM-dd')
    }
    order['orderEndTime'] = formatDate(item.service_end_time, 'yyyy-MM-dd')
    order['payStatus'] = payStatusMap[item.pay_status]
    order['serviceStatus'] = serviceStatusMap[item.service_status]
    return order
  })
  return result
}

