// 服务订单--订单详情--阿姨信息记录数据转换
export const transAyiInfos = (data, statusMap) => {
  console.log(`transAyiInfos`)
  let serviceStatusMap = statusMap.serviceStatus
  let records = data.map(item => {
    let record = {}
    let service = item.service
    let orderObject = item.order_object || {}
    record['orderNumber'] = item.code
    record['ayiId'] = item.service_id
    if (service) {
      record['ayiName'] = service.title
      if (service.housework_company) {
        record['companyId'] = service.housework_company.id
        record['domesticCompany'] = service.housework_company.title
      }
    }
    record['serviceFee'] = item.agency_price
    record['ayiSalary'] = toCurrencyStr(item.base_price)
    record['salarySettlingDay'] = item.pay_settlement_date
    record['ayiInsurance'] = item.insurance
    if (item.service_apply_time) {
      record['serviceStartTime'] = formatDate(item.service_apply_time, 'yyyy-MM-dd')
    } else {
      record['serviceStartTime'] = formatDate(item.service_time, 'yyyy-MM-dd')
    }
    record['serviceEndTime'] = formatDate(item.service_end_time, 'yyyy-MM-dd')
    record['subOrderStatus'] = serviceStatusMap[item.service_status]
    record['contract'] = orderObject.pdf_url
    return record
  })
  return records
}

// 阿姨工资记录属性字段
const ayiSalaryFields = [
  { prop: 'tradingHours', label: '交易时间' },
  { prop: 'ayiId', label: '阿姨ID' },
  { prop: 'ayiName', label: '阿姨姓名', action: 'gotoAyiDetail' },
  { prop: 'payeeAccount', label: '收款人账号' },
  { prop: 'tradingAmount', label: '交易金额' },
  { prop: 'businessDocNumber', label: '商户单号' },
  { prop: 'isAreadyArrived', label: '是否已到账' }
]

// 服务订单--订单详情--阿姨工资记录数据转换
export const transAyiSalaryRecord = data => {
  console.log(`transAyiSalaryRecord`)
  let records = data.map(item => {
    let record = {}
    let ayiInfo = item.service_object
    if (ayiInfo) {
      record['ayiId'] = ayiInfo.id
      record['ayiName'] = ayiInfo.title
      record['payeeAccount'] = ayiInfo.phone
    }
    record['tradingHours'] = formatDate(item.create_time, 'yyyy-MM-dd')
    record['tradingAmount'] = toCurrencyStr(item.salary)
    record['businessDocNumber'] = item.code
    record['isAreadyArrived'] = item.is_payoff ? '是' : '否'
    return record
  })
  return records
}

