// 服务订单--订单详情--雇主付款记录数据转换
export const transEmPaidRecord = data => {
  let records = data.map(item => {
    let record = {}
    record['tradingHours'] = formatDate(item.create_time, 'yyyy-MM-dd')
    record['payType'] = item.pay_type === 'wechat' ? '微信' : '支付宝'
    record['payee'] = `得贤平台`
    record['payeeAccount'] = `得贤平台微信商户号`
    record['tradingAmount'] = toCurrencyStr(item.price)
    record['businessDocNumber'] = item.order_code
    record['isAreadyArrived'] = item.bill_status === 'finished' ? '是' : '否'
    return record
  })
  return records
}

