<template>
  <el-upload
    class="avatar-uploader needsclick"
    action="default"
    name="photo_id"
    :http-request="uploadAction"
    :headers="uploadHeaders"
    :show-file-list="false"
    :on-error="handleError"
    :before-upload="beforeAvatarUpload">
    <slot></slot>
  </el-upload>
</template>
<script>
  /** @author 吴永军
   * @description 上传单张图片组件
  */
  import { uploadImage, ossUpload, uploadFile } from '../service/loginServer'
  import { getLoginInfo } from '../assets/common/utils'
  export default {
    name: 'uploadImage',
    props: ['use', 'imageSize'],
    data () {
      return {
        uploadHeaders: {
          'Authentication-Token': getLoginInfo(`Authentication-token`)
        }
      }
    },
    created () {},
    methods: {
      // 覆盖默认上传行为
      uploadImg (item) {
        let fd = new FormData()
        fd.append('photo_id', item.file)
        uploadImage(fd).then(res => {
          let params = {
            path: res.data.file_path,
            use: this.use
          }
          // 更改图片路径
          this.$store.dispatch('commitChangeImage', params.path)
          // 通知父级元素，返回图片路径
          this.$emit('uploadSucc', params)
        })
      },
      uploadAction (item) {
        let ossParams = { 'upload_type': 'admin' }
        ossUpload(ossParams).then(res => {
          let params = {
            host: res.data.host
          }
          let fd = new FormData()
          let postfix = item.file.name.split('.')[item.file.name.split('.').length - 1]
          let filename = this.$md5(`${new Date().getTime()}_${item.file.name}`)
          fd.append('callback', res.data.callback)
          fd.append('success_action_status', '200')
          fd.append('OSSAccessKeyId', res.data.accessid)
          fd.append('policy', res.data.policy)
          fd.append('signature', res.data.signature)
          fd.append('key', `${res.data.dir}${filename}.${postfix}`)
          fd.append('host', res.data.host)
          fd.append('file', item.file)
          fd.append('expire', res.data.expire)
          fd.append('name', `${filename}.${postfix}`)
          params['fd'] = fd
          uploadFile(params.host, params.fd).then(res => {
            let result = {
              path: `${params.host}/${res.filename}`,
              host: `${params.host}`,
              use: this.use
            }
            // 更改图片路径
            this.$store.dispatch('commitChangeImage', result.path)
            // 通知父级元素，返回图片路径
            this.$emit('uploadSucc', result)
          })
        })
      },
      // 证件照上传钱处理函数
      beforeAvatarUpload (file) {
        const isLtImageSize = file.size / 1024 / 1024 < this.imageSize
        const isCurType = /\.(jpg|jpeg|png|bmp|JPG|PNG|BMP)$/.test(file.name)
        if (!isLtImageSize) {
          this.$alert(`上传图片大小不能超过 ${this.imageSize}MB!`, '友情提示', {
            confirmButtonText: '确 定',
            callback: action => {}
          })
        }
        if (!isCurType) {
          this.$alert('上传图片类型不符合要求!', '友情提示', {
            confirmButtonText: '确 定',
            callback: action => {}
          })
        }
        return isLtImageSize && isCurType
      },
      handleError () {
        this.$alert('图片上传失败！', '消息提示', {
          confirmButtonText: '确定',
          callback: action => {}
        })
      }
    }
  }
</script>
<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 278px;
    height: 178px;
    line-height: 178px;
    text-align: center;
    border: 1px solid;
  }
  .avatar {
    width: 278px;
    height: 178px;
    display: block;
  }
</style>



