import instance from './http.config.js'
/**
 * @author 吴永军
 * @description 登录、注册、家政公司入驻接口
 *  */

// 登录服务
export const login = params => instance.post('/admin/company_login/dologin', params)

// 登出操作
export const loginout = () => instance.get(`/admin/index/logout`)

// 获取手机验证码
export const getValidateCode = phone => instance.get(`/admin/company_user/send_code?account_number=${phone}`)

// 注册服务
export const regist = params => instance.post(`/admin/company_user`, params)

// 获取省份信息
export const getProvinces = () => instance.get(`/admin/service/getProvinceRegionList?pid=-1`)

// 根据省份信息获取市信息
export const getCities = provinceId => instance.get(`/admin/service/getProvinceRegionList?pid=${provinceId}`)

// 根据市信息获取区信息
export const getCountries = cityId => instance.get(`/admin/service/getProvinceRegionList?pid=${cityId}`)

// 上传文件oss
export const ossUpload = params => instance.post(`/upload/oss`, params)

// 上传图片
export const uploadFile = (host, fd) => instance.post(host, fd, {
  headers: {
    'Content-Type': 'multipart/form-data'
  }
})
// 上传图片方法
export const uploadImage = fd => instance.post(`/upload/index`, fd, {
  headers: {
    'Content-Type': 'multipart/form-data'
  }
})
export const uploadoss = params => instance.post(`/upload/oss`, params)

// 提交入驻公司信息
export const saveCompanyInfo = params => instance.post(`/admin/company`, params)



