import instance from './http.config.js'
// 获取上户状态
export const getStatusService = () => instance.get(`/admin/service/service_status`)
// 解除拉入黑名单
export const setBlackService = (param) => instance.post(`/admin/service/set_blacklist_status`, param)
// 获取阿姨列表
export const getAuntListService = (param) => instance.get(`/admin/service/index?${param}`)
// 获取籍贯
export const getPlaceService = (param) => instance.get(`/admin/service/getProvinceRegionList?pid=${param.id}`)
// 获取服务类别列表
export const getTypeService = () => instance.get(`/admin/service_type/index`)
// 获取等级
export const getLevelService = (param) => instance.get(`/admin/service_salary/get_levels?type=${param.type}`)
// 获取技能列表
export const getSkillService = (param) => instance.get(`/admin/service_babel/index??page=1&per_page=1000&category=${param.category}&service_type_id=${param.service_type_id}`, param)
// 获取性格列表
export const getCharacterService = () => instance.get(`/admin/nature/index?page=1&per_page=1000&status=1`)
// 添加阿姨性格
export const addCharacterService = (param) => instance.post(`/admin/nature/save`, param)
// 添加证书技能接口
export const addSkillCertService = (param) => instance.post(`/admin/service_babel/add`, param)
// 获取证书列表
export const getCertificateService = (param) => instance.get(`/admin/service_babel/index?page=1&per_page=1000&category=${param.category}&service_type_id=${param.service_type_id}`, param)
// 添加阿姨
export const addAunt = (param) => instance.post(`/admin/service/addServicePeopleInfo`, param)
// 上下架
export const setShelveStatus = (param) => instance.put(`/admin/service/set_shelve_status`, param)
// 删除证书工作风采
export const removeImage = (param) => instance.delete(`/admin/service/delServiceCert`, {data: param})
// 获取阿姨详情
export const getDetailService = (param) => instance.get(`/admin/service/service?id=${param}`, param)
// 修改阿姨
export const updateAunt = (param) => instance.put(`/admin/service/service`, param)

// 获取阿姨相关订单记录
export const getOrderService = (param) =>
instance.get(`/admin/service/order_list?service_id=${param.id}&page=${param.page}&per_page=${param.per_page}`)

// 获取阿姨提现记录
export const getWithdrawService = (param) =>
instance.get(`/admin/service/get_withdraw_list?service_id=${param.id}&page=${param.page}&per_page=${param.per_page}`)

// 获取基本信息条件---学历列表、工作经验列表和服务类型列表
export const getBasicInformation = () => instance.get(`/admin/service/conditionList`)

