import instance from './http.config.js'
// 获取oss上传文件参数
export const ossUpload = params => instance.post(`/upload/oss`, params)

// 上传图片
export const uploadImage = fd => instance.post(`/upload/index`, fd, {
  headers: {
    'Content-Type': 'multipart/form-data'
  }
})

// 上传图片
export const uploadFile = (host, fd) => instance.post(host, fd, {
  headers: {
    'Content-Type': 'multipart/form-data'
  }
})

// 获取家政公司信息
export const obtailEssentialInfo = (companyId) => instance.get(`/admin/company/info?company_id=${companyId}`)

// 获取省市级地址
export const obtainProvinceAddress = str => instance.get('/admin/service/getProvinceRegionList' + str)

// 更改家政公司信息
export const updateEssentialInfo = params => instance.put('/admin/company', params)

// 获取管理员列表
export const obtainAccountData = (str) => instance.get(`/admin/user/index` + str)

// 删除管理员
export const deleteAccount = params => instance.post('/admin/user/userDel', params)

// 启用禁用管理员
export const accountStatus = params => instance.put('/admin/user/userStatus', params)

// 获取管理员角色
export const obtainAdminRoleData = str => instance.get('/admin/role/index' + str)

// 添加管理员
export const addAdminRole = params => instance.post('/admin/user/userAdd', params)

// 修改用户信息
export const updateUserInfo = params => instance.put('/admin/user/userEdit', params)

// 修改手机号获取验证码
export const sentPhoneVerifyCode = userName => instance.get(`/admin/user/send_code?user_name=${userName}`)

// 头像列表
export const obtainPortraitData = str => instance.get('/admin/head_photo' + str)



