<template>
  <div id="order-table">
    <div>
      <el-table :data="tableData" :height="tableHeight" border style="width: 100%;">
        <template v-for="(value, index) in tableFields">
          <!--普通属性列：主要用于展示信息-->
          <el-table-column :prop="value.prop" :label="value.label" :key="index" :width="value.width"
            v-if="value.prop !== 'opers' && value.prop !== 'ayiName'" header-align="center" align="center"></el-table-column>
          <!--阿姨属性列：用于跳转到阿姨详情-->
          <el-table-column :label="value.label" :key="index"
            v-if="value.prop === 'ayiName'" header-align="center" align="center">
            <template slot-scope="scope">
              <el-button :key="index"
                type="text" slot="reference" size="medium"
                @click="act(value.action, scope.row)">{{scope.row.ayiName}}</el-button>
            </template>
          </el-table-column>
          <!--普通操作列：主要用于提供各种操作交互-->
          <el-table-column :key="index" :label="value.label"
            v-if="value.prop === 'opers'" header-align="center" align="center">
            <template slot-scope="scope">
              <el-button :key="index"
                v-for="(oper, index) in value.opers" type="text" slot="reference" size="medium"
                @click="operate(oper.type, scope.row)">{{oper.name}}</el-button>
            </template>
          </el-table-column>
        </template>
      </el-table>
    </div><br>
    <div class="pagination" v-if="isPagination">
      <el-pagination :current-page="currentPage" :page-sizes="[10, 20, 30]" :page-size="pageSize" :total="total"
        layout="prev, pager, next, jumper" background
        @size-change="handleSizeChange" @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>
<script>
/**
 * @author 吴永军
 * @description 订单管理相关功能表格组件
 *  */
export default {
  name: 'orderTable',
  props: ['tableData', 'tableHeight', 'isPagination', 'page', 'total', 'fields'],
  data () {
    return {
      currentPage: 1,
      pageSize: 10,
      tableFields: []
    }
  },
  watch: {
    page (oldVal, newVal) {
      if (newVal) {
        this.currentPage = this.page
      }
    },
    fields (oldVal, newVal) {
      if (newVal) {
        this.tableFields = this.fields
      }
    }
  },
  created () {
    this.tableFields = this.fields
  },
  methods: {
    handleSizeChange (val) {
      this.pageSize = val
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.$emit('changeCurrentPage', this.currentPage)
    },
    operate (type, obj) {
      console.log(type, obj)
      this.$emit(type, obj)
    },
    act (type, obj) {
      this.$emit(type, obj)
    }
  }
}
</script>
<style scoped>
@import "../../../assets/styles/common.css";
.pagination {
  text-align: center;
}
</style>



