/**
 * @author 吴永军
 * @description 订单管理模块相关接口
 */
import instance from './http.config.js'

// 获取服务类型
export const getServiceTypes = () => instance.get(`/admin/service_type/index`)

// 获取支付状态
export const getPayStatus = () => instance.get(``)

// 获取服务状态
export const getServiceStatus = () => instance.get(``)

// 获取服务订单查询条件
export const getSerOrdersConditions = () => instance.get(`/admin/orders/conditionList`)

// 获取服务订单--分页
export const getServiceOrders = paramStr => instance.get(`/admin/orders/index?${paramStr}`)

// 获取服务订单详情
export const getSerOrderDetail = orderId => instance.get(`/admin/orders/info?order_id=${orderId}`)

// 获取服务订单详情下的相关记录表格
export const getOrderRelatedRecords = (api, orderId) => instance.get(`${api}?order_id=${orderId}`)

// 获取课程订单--分页
export const getCourseOrders = params =>
  instance.get(`/admin/orders/index?page=${params.page}&&per_page=${params.perPage}`)

// 获取订单相关阿姨列表记录
export const getAyiInfos = orderId => instance.get(`/admin/orders/service_info?order_id=${orderId}`)

// 获取订单相关阿姨工资列表记录
export const getAyiSalaryRecords = orderId => instance.get(`/admin/records/service_salary?order_id=${orderId}&page=1&per_page=1000`)

// 获取订单相关雇主支付列表记录
export const getEmPaidRecords = orderId => instance.get(`/admin/records/employer_pay_record?order_id=${orderId}&page=1&per_page=1000`)

// 获取订单相关雇主退款列表记录
export const getEmRefundRecords = orderId => instance.get(`/admin/orders/info?order_id=${orderId}`)

// 获取订单相关分销列表记录
export const getDistributionRecords = orderId => instance.get(`/admin/orders/info?order_id=${orderId}`)

// 获取订单相关服务费更列表记录
export const getServiceRecords = orderId => instance.get(`/admin/orders/info?order_id=${orderId}`)

// 获取订单相关订单变更列表记录
export const getOrderChangedRecords = orderId => instance.get(`/admin/orders/info?order_id=${orderId}`)

// 获取订单相关申诉列表记录
export const getOrderAppealRecords = orderId => instance.get(`/admin/orders/info?order_id=${orderId}`)



