import { toCurrencyStr, formatDate, changeDateToStr } from '../../../service/common'
// 时间范围控件快捷键配置
export const shortcuts = [
  {
    text: '最近一周',
    onClick (picker) {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
      picker.$emit('pick', [start, end])
    }
  },
  {
    text: '最近一个月',
    onClick (picker) {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
      picker.$emit('pick', [start, end])
    }
  },
  {
    text: '最近三个月',
    onClick (picker) {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
      picker.$emit('pick', [start, end])
    }
  }
]

// 创建服务订单服务状态和支付状态描述信息对照表
export const makeStatusMap = conditions => {
  let status = {
    serviceStatus: {},
    payStatus: {}
  }
  conditions.service_status.forEach(item => {
    status.serviceStatus[item.category] = item.name
  })
  conditions.pay_status.forEach(item => {
    status.payStatus[item.category] = item.name
  })
  return status
}

// 转换查询参数方法
export const transParamsToStr = params => {
  let paramStr = `page=${params.page}&per_page=${params.perPage}`
  if (params.type && params.type !== -1) {
    paramStr += `&service_type_id=${params.type}`
  }
  if (params.serviceStatus && params.serviceStatus !== 'all') {
    paramStr += `&service_status=${params.serviceStatus}`
  }
  if (params.payStatus && params.payStatus !== 'all') {
    paramStr += `&pay_status=${params.payStatus}`
  }
  if (params.employer) {
    paramStr += `&user_name=${params.employer}`
  }
  if (params.serviceTime) {
    let serviceStartTime = changeDateToStr(params.serviceTime[0])
    let serviceEndTime = changeDateToStr(params.serviceTime[1])
    paramStr += `&service_time=${serviceStartTime}&service_end_time=${serviceEndTime}`
  }
  return paramStr
}

// 服务订单详情数据转换
export const transServiceOrderDetail = (data = {}, statusMap = {}) => {
  let orderDetail = {}
  let orderObject = data.order_object || {}
  let serviceStatusMap = statusMap.serviceStatus
  let payStatusMap = statusMap.payStatus
  orderDetail['id'] = orderObject.order_id
  orderDetail['number'] = data.code
  orderDetail['serviceType'] = orderObject.type_name
  orderDetail['orderTime'] = formatDate(data.create_time, 'yyyy年MM月dd日')
  orderDetail['employerId'] = data.user_id
  orderDetail['employerName'] = orderObject.user_name
  orderDetail['employerPhone'] = orderObject.user_phone
  orderDetail['employerAddr'] = orderObject.user_addr
  orderDetail['payStatus'] = payStatusMap[data.pay_status]
  orderDetail['serviceStatus'] = serviceStatusMap[data.service_status]
  orderDetail['orderAmount'] = toCurrencyStr(orderObject.service_salary)
  // orderDetail['premium'] = orderObject.id
  orderDetail['asideIn'] = orderObject.live_home
  if (data.service_apply_time) {
    orderDetail['serviceStartTime'] = formatDate(data.service_apply_time, 'yyyy年MM月dd日')
  } else {
    orderDetail['serviceStartTime'] = formatDate(data.service_time, 'yyyy年MM月dd日')
  }
  orderDetail['serviceEndTime'] = formatDate(data.service_end_time, 'yyyy年MM月dd日')
  orderDetail['orderDuration'] = orderObject.type_name === '母婴护理师' ? `${orderObject.service_days}天`
    : `${Math.floor(orderObject.service_days / 30)}个月`
  orderDetail['backup'] = orderObject.remark
  return orderDetail
}



